import StringUtils from "../../utils/StringUtils";
import VueContext from "../../context/VueContext";
import ConstantsStatusCodesHttp from "../../constants/ConstantsStatusCodesHttp";
import ResponseService from "../../beans/ResponseService";
import CommonsUtils from "../../utils/CommonsUtils";
/**
 * Base class for al services
 */
export default class BaseService {
  constructor(baseHost, pathService) {
    this.baseHost = baseHost;
    this.pathService = pathService;
  }

  /**
   * Method for get path service
   * @returns path for service
   */
  getPathService() {
    return this.pathService;
  }

  /**
   * Method for get base host for service
   * @returns base host for service
   */
  getBaseHost() {
    let baseHost = "";
    // In elecontr apps dont use base host
    if (!VueContext.isElectronApp()) {
      baseHost = this.baseHost;
      if (StringUtils.isBlank(baseHost)) {
        baseHost = VueContext.baseHost;
      }
    }
    return baseHost;
  }

  /**
   * Method for get real path host for service
   * @returns real path host for service
   */
  getRealPathHost() {
    return this.getBaseHost() + this.getPathService();
  }

  /**
   * Method to get request
   * @param {*} url
   * @param {*} bodyData
   * @param {*}
   */
  async sendFetchRequest(url, bodyData, type) {
    if (type == null || type == undefined) {
      type = "POST";
    }
    let options = {
      method: type,
      headers: this.getHeaders(),
    };
    if (bodyData != null && bodyData != undefined) {
      options.body = bodyData;
    }

    let request = null;

    // Vuecontext fetch to electron
    if (VueContext.isElectronApp()) {
      options.url = url;
      request = options;
      return VueContext.fetchElectron(request);
    } else {
      request = new Request(url, options);
      return fetch(request);
    }
  }

  /**
   * Method to get headers for send request
   */
  getHeaders() {
    let headers = null;

    if (VueContext.isElectronApp()) {
      headers = {};
    } else {
      headers = new Headers();
      headers.append("Content-Type", "application/json");
      headers.append("Accept", "application/json");
    }

    return headers;
  }

  /**
   * Method for resolve data acess await
   * @param {*} fnData
   * @param {*} keyResolveData
   */
  async resolveDataAccessAwait(fnData, keyResolveData) {
    if (!CommonsUtils.isNotNull(keyResolveData)) {
      keyResolveData = "data";
    }
    const dataReturn = new ResponseService();
    try {
      let data = await fnData();
      // check status http is 200 or 201
      if (VueContext.isElectronApp()) {
        data = JSON.parse(data);
        if (
          CommonsUtils.isNotNull(data) &&
          (data.statusProp === ConstantsStatusCodesHttp.OK ||
            data.statusProp === ConstantsStatusCodesHttp.CREATED)
        ) {
          const json = data.jsonProp;

          dataReturn.data = json[keyResolveData];
          dataReturn.statusCode = data.statusProp;
        } else if (CommonsUtils.isNotNull(data)) {
          dataReturn.statusCode = data.statusProp;
        }
      } else {
        if (
          CommonsUtils.isNotNull(data) &&
          (data.status === ConstantsStatusCodesHttp.OK ||
            data.status === ConstantsStatusCodesHttp.CREATED)
        ) {
          const json = await data.json();

          dataReturn.data = json[keyResolveData];
          dataReturn.statusCode = data.status;
        } else if (CommonsUtils.isNotNull(data)) {
          dataReturn.statusCode = data.status;
        }
      }
    } catch (ex) {
      dataReturn.errorMsg = ex.message;
    }
    return dataReturn;
  }
}
