import CommonsUtils from "../../utils/CommonsUtils";
/**
 * Factory to resolve menu
 */
export default class MenuFactory {
  /**
   * Method to show hde menu level 1
   * @param vueContext
   * @param id
   * @param title
   * @param componentView
   * @param moduleName
   */
  async onMenuClickLevel1(vueContext, id, title, componentView, moduleName) {
    const element = document.getElementById(id);
    let elementParent = null;
    let elementParentTmp = element.parentElement;

    const maxLoopMenu = 10;
    let counterLoopMenu = 0;

    // shearch active parent
    while (elementParent == null && counterLoopMenu < maxLoopMenu) {
      elementParentTmp = elementParentTmp.parentElement;
      if (elementParentTmp.classList.contains("MenuActive")) {
        elementParent = elementParentTmp;
        break;
      }
      counterLoopMenu++;
    }

    const menuActive = document.querySelectorAll(".MenuActive");

    if (CommonsUtils.isNotNull(menuActive)) {
      for (let i = 0; i < menuActive.length; i++) {
        menuActive[i].classList.remove("MenuActive");
      }
    }
    element.classList.add("MenuActive");
    elementParent.classList.add("MenuActive");

    const icon = elementParent.getElementsByClassName("MenuItemLevelIcon0")[0];
    icon.classList.remove("fa-caret-right");
    icon.classList.add("fa-caret-down");

    vueContext.applyAsyncFunctionWithHandlerViewError(() => {
      return vueContext.addTab(title, componentView, moduleName);
    });
  }

  /**
   * Method to show hide menu level 0
   * @param id
   */
  onMenuClickLevel0(id) {
    const element = document.getElementById(id);

    const menuActive = document.querySelectorAll(".MenuActive");
    if (CommonsUtils.isNotNull(menuActive)) {
      for (let i = 0; i < menuActive.length; i++) {
        if (menuActive[i].id != id) {
          menuActive[i].classList.remove("MenuActive");
        }
      }
    }

    if (element.classList.contains("MenuActive")) {
      element.classList.remove("MenuActive");
      const icon = element.getElementsByClassName("MenuItemLevelIcon0")[0];
      icon.classList.remove("fa-caret-down");
      icon.classList.add("fa-caret-right");
    } else {
      element.classList.add("MenuActive");
      const icon = element.getElementsByClassName("MenuItemLevelIcon0")[0];
      icon.classList.remove("fa-caret-right");
      icon.classList.add("fa-caret-down");
    }
  }
}
