<!--

Component for menu imtes

-->

<template>
  <!-- Level 0 -->
  <div v-if="level == 0" :id="getId()" class="MenuItemLevel0" level="0">
    <i class="MenuItemLevelIcon0 fas fa-caret-right"></i>

    <div class="MenuItemLevelTitle0" v-on:click="onClickItemMenu()">{{label}}</div>

    <div class="MenuItemLevelItems0">
      <!--Content for menu-->
      <slot name="menuItemContentTemplate"></slot>
    </div>
  </div>

  <!-- Level 1 -->
  <div v-else-if="level == 1" :id="getId()" class="MenuItemLevel1" level="1">
    <div class="MenuItemLevelTitle1" v-on:click="onClickItemMenu()">{{label}}</div>
  </div>
</template>

<script>
import BaseComponent from "../base/BaseComponent";
export default {
  extends: BaseComponent,
  name: "MenuItemComponent",
  props: {
    // Level for menu
    level: {
      type: Number,
      default: 0
    },
    // label for menu
    label: {
      type: String,
      default: ""
    },
    // ComponentView for menu
    componentView: {
      type: String,
      default: null
    },
    // Module from component view for menu
    module: {
      type: String,
      default: null
    }
  },
  methods: {
    /**
     * Method to click menu
     */
    onClickItemMenu: function() {
      switch (this.level) {
        case 0:
          this.onClickItemMenuLevel0();
          break;
        case 1:
          this.onClickItemMenuLevel1();
          break;
      }
    },
    /**
     * Method to click menu level 0
     */
    onClickItemMenuLevel0: function() {
      this.getVueContext().menuFactory.onMenuClickLevel0(this.getId());
    },
    /**
     * Method to click menu level 1
     */
    onClickItemMenuLevel1: async function() {
      await this.getVueContext().menuFactory.onMenuClickLevel1(
        this.getVueContext(),
        this.getId(),
        this.label,
        this.componentView,
        this.module
      );
    }
  }
};
</script>

<style scoped>
.MenuItemLevel,
.MenuItemLevel0,
.MenuItemLevelItems0,
.MenuItemLevel1 {
  width: 100%;
  height: 30px;
  float: left;
  clear: left;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(255, 255, 255, 0.07);
}

.MenuItemLevelItems0 {
  border-bottom: 1px solid rgba(255, 255, 255, 0.07);
  height: auto;
}

.MenuItemLevel:hover,
.MenuItemLevel0:hover,
.MenuItemLevel1:hover {
  cursor: pointer;
}

.MenuItemLevel0 {
  height: auto;
}

.MenuItemLevel1 {
  border-bottom: none;
}

.MenuItemLevelTitle,
.MenuItemLevelTitle0,
.MenuItemLevelTitle1 {
  float: left;
  margin: 5px;
  padding: 0;
  box-sizing: border-box;
}

.MenuItemLevelTitle0,
.MenuItemLevelTitle1 {
  font-size: 18px;
  font-weight: bold;
}

.MenuItemLevelTitle1 {
  margin-left: 28px;
  font-size: 16px;
}

.MenuItemLevelIcon0 {
  margin-left: 8px;
  margin-top: 8px;
  margin-bottom: 5px;
  padding: 0;
  box-sizing: border-box;
  float: left;
}

.MenuItemLevelItems0 {
  display: none;
  width: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  float: left;
}

.MenuActive {
}

.MenuActive:hover {
}

.MenuActive .MenuItemLevelItems0 {
  display: block;
}
</style>