import I18n from "../../../i18n/I18n";
import BaseSecurityService from "../../../service/basesecurityservice/BaseSecurityService";
import MenuFactory from "../../../factory/menu/MenuFactory";
import HeaderFactory from "../../../factory/header/HeaderFactory";
import CommonsUtils from "../../../utils/CommonsUtils";
/**
 * Class for manage properties in context application
 */
export default class PropertiesContextFactory {
  constructor() {
    // App name
    this.appName = "RFVue";
    // App have secuirty
    this.haveSecurity = true;
    // App component for application
    this.appComponent = null;
    // I18n for transalte text
    this.i18n = new I18n();
    // Log url
    this.logUrl = "/secure/log";
    // Login url
    this.loginUrl = "login";
    // Refresh token url
    this.refreshTokenUrl = "refreshToken";
    // Time expiration token
    this.timeExpireToken = 60 * 20 * 1000;
    // this.timeExpireToken = 1;
    // Date last request
    this.dateLastRequest = null;
    // Base host
    this.baseHost = "http://localhost:3000/rfpm/";
    // Recors per page default
    this.totalRecordsFindListDefault = 50;
    // Pages viisibles in list datatables
    this.pageVisiblesInListDefault = 5;
    // Array tabs
    this.tabs = [];
    // Active tab selected
    this.activeTab = -1;
    // Lazy import components
    this.mapLazyImport = {};
    // Lazy imports modals
    this.mapModalsLazyImport = {};
    // Lazy import Service
    this.mapLazyImportServices = {};
    // Lazy import Validators
    this.mapLazyImportValidators = {};
    // Lazy import Converters
    this.mapLazyImportConverters = {};
    // Services
    this.services = {};
    // Map extra data
    this.mapExtraData = {};
    // Default locaale
    this.locale = "en";
    // Converters
    this.converters = {};
    // validators
    this.validators = {};
    // Set base security service
    this.securityService = new BaseSecurityService();
    // Menu factory
    this.menuFactory = new MenuFactory();
    // Security interceptor
    this.secuirtyInterceptor = null;
    // Z-index
    this.zIndex = 10;
    // header factory
    this.headerFactory = new HeaderFactory();
    // Title for application
    this.titleApp = "RFVue";
    // Helpder jwt
    this.jwtHelperService = require("jsonwebtoken");
    // Map public url domains
    this.mapUrlsPublic = {};
    // Is Electron app
    this.elecontroApp = false;
    // Ipc render for electron
    this.ipcRender = null;
  }

  /**
   * Method to get active tab
   * @returns Active tab if selected, null if not found
   */
  getActiveTab() {
    return this.activeTab < this.tabs.length && this.activeTab > -1
      ? this.tabs[this.activeTab]
      : null;
  }
  /**
   * Method for get map extra data
   * @returns map extra data
   */
  getMapExtraData() {
    return this.mapExtraData;
  }
  /**
   * Method for get extra data
   * @param {*} key from extra data
   * @returns extra data from key
   */
  getExtraData(key) {
    return this.mapExtraData[key];
  }
  /**
   * Method for hide wait status
   * @param {*} tab for hide wait status
   */
  async hideWaitStatus(tab) {
    tab != null;
  }

  /**
   * Method to know is electron app
   * @returns true if is electron app false if not
   */
  isElectronApp() {
    return (
      this.elecontroApp != null &&
      this.elecontroApp != undefined &&
      this.elecontroApp
    );
  }

  /**
   * Method to set app is electron
   * @param {*} isElecotronApp true if is electron app false if not
   */
  setElectronApp(isElecotronApp) {
    if (CommonsUtils.isNotNull(isElecotronApp)) {
      this.elecontroApp = isElecotronApp;
    }
  }

  /**
   * Method for get base host
   * @returns If is elecotrn app retun empty string else return base host property
   */
  getBaseHost() {
    return this.isElectronApp() ? "" : this.baseHost;
  }

  /**
   * Method for get ipc render for electron
   */
  getIpcRender() {
    if (
      this.isElectronApp() &&
      (this.ipcRender == null || this.ipcRender == undefined)
    ) {
      this.ipcRender = window.require("electron").ipcRenderer;
    }
    return this.ipcRender;
  }
}
