import BaseService from "../baseservice/BaseService";
import StringUtils from "../../utils/StringUtils";
import CommonsUtils from "../../utils/CommonsUtils";
import VueContext from "../../context/VueContext";
import ConstantsStatusCodesHttp from "../../constants/ConstantsStatusCodesHttp";
/**
 * Base class for serucirty operations
 */
export default class BaseSecurityService extends BaseService {
  constructor() {
    super(null, null);
  }

  /**
   * Method for get path service sercurity login
   */
  getPathLoginUrl() {
    return VueContext.loginUrl;
  }

  /**
   * Method for get path for refresh token
   */
  getPathRefreshTokenUrl() {
    return VueContext.refreshTokenUrl;
  }

  /**
   * Method for login in application
   * @param {*} userName is the user name for logging
   * @param {*} password is the nick for logging
   * @param {*} fnPostLogin function execute post logging
   * @param {*} params params send in logging
   */
  async login(userName, password, fnPostLogin, params) {
    let valid = false;
    VueContext.appComponent.logged = false;
    try {
      // Check have login url to call login rest servie
      if (StringUtils.isNotBlank(this.getPathLoginUrl())) {
        const body = { user: userName, password: password, params: params };
        const data = await this.resolveDataAccessAwait(() => {
          return this.sendFetchRequest(
            this.getBaseHost() + this.getPathLoginUrl(),
            JSON.stringify(body),
            null
          );
        });

        // If login ok set token an data if necesary in session
        if (
          CommonsUtils.isNotNull(data) &&
          data.statusCode === ConstantsStatusCodesHttp.OK
        ) {
          valid = true;
          VueContext.setTokenInSession(data.data["token"]);
          VueContext.initDataInSession();
          // If have funciont execute after login call this
          if (CommonsUtils.isNotNull(fnPostLogin)) {
            fnPostLogin(data);
          }
          // if ko romove token and data in session
        } else {
          VueContext.removeDataInSession();
          VueContext.removeTokenInSession();
        }
      }
    } catch (ex) {
      //console.log(ex);
      // On error remove token an data in session
      VueContext.removeDataInSession();
      VueContext.removeTokenInSession();
      valid = false;
    }
    return valid;
  }
  /**
   * Mehtod for refresh token
   */
  async refreshToken() {
    try {
      // Check have refresh token url for call rest service
      if (StringUtils.isNotBlank(VueContext.refreshTokenUrl)) {
        const body = { token: VueContext.getTokenInSession() };
        // console.log(body);
        const data = await this.resolveDataAccessAwait(() => {
          return this.sendFetchRequest(
            this.getBaseHost() + this.getPathRefreshTokenUrl(),
            JSON.stringify(body),
            null
          );
        });
        // console.log(data);
        // if ok set refresh token in session
        if (CommonsUtils.isNotNull(data) && data.status === 200) {
          VueContext.setTokenInSession(data.data);
          VueContext.dateLastRequest = new Date();
        }
      }
    } catch (ex) {
      console.log(ex);
    }
  }

  /**
   * Method for logout in application
   */
  logout() {
    // remove token and data from session
    VueContext.removeDataInSession();
    VueContext.removeTokenInSession();
    VueContext.clearApp();
  }

  /**
   * Method to know is valid session
   */
  validSession() {
    const token = VueContext.getTokenInSession();
    // check have token in session
    return StringUtils.isNotBlank(token);
  }
}
