<!--
 Base component for create inputs
-->
<script>
import BaseComponent from "../BaseComponent";
import CommonsUtils from "../../../utils/CommonsUtils";
import StringUtils from "../../../utils/StringUtils";
import ConstantsComponents from "../../../constants/ConstantsComponents";

export default {
  extends: BaseComponent,
  name: "BaseInputComponent",

  props: {
    // Label for input
    label: {
      type: String,
      default: ""
    },
    // Disabled true or false
    disabled: {
      type: Boolean,
      default: false
    },
    // required true or false
    required: {
      type: Boolean,
      default: false
    },
    // size
    size: {
      type: Number,
      default: 0
    },
    // max
    max: {
      type: Number,
      default: 524288
    },
    // min
    min: {
      type: Number,
      default: -1
    },
    // rendered show or not
    rendered: {
      type: Boolean,
      default: true
    },
    // renderLabel show label or not
    renderLabel: {
      type: Boolean,
      default: true
    },
    // Class css for input
    styleClass: {
      type: String,
      default: ""
    },
    // Element contains data for input
    element: {
      type: Object,
      default: null
    },
    // Propertie for set data in element
    property: {
      type: String,
      default: ""
    },
    // Function find data on search
    functionFind: {
      type: Function,
      default: null
    },
    // Function execute after find data on search
    functionAfterFind: {
      type: Function,
      default: null
    },
    // Array names for validators
    validators: {
      type: Object,
      default: null
    },
    // Array names for converters
    converters: {
      type: Object,
      default: null
    },
    // Name for service for find data
    service: {
      type: String,
      default: null
    },
    // Map properties find
    mapPropertiesFind: {
      type: Object,
      default: null
    },
    // Id for button search data
    inputButton: {
      type: String,
      default: null
    },
    // Property for find data for input
    propertyFind: {
      type: String,
      default: "code"
    },
    // Fiter find data for input
    filterFind: {
      type: Object,
      default: null
    },
    // placeholder input
    placeholder: {
      type: String,
      default: ""
    }
  },

  methods: {
    /**
     * Method for get type for input
     * @returns type for input
     */
    getType: function() {
      return null;
    },

    /**
     * Method for get default class
     * @returns default class for input
     */
    getDefaultStyleClass: function() {
      return "";
    },

    /**
     * Method for take model and propertie set value
     * @returns array for model and propertie for model
     */
    takeModelProperties: function() {
      let properties = null;
      let model = null;

      if (CommonsUtils.isNotNull(this.element)) {
        model = this.element;
        properties = this.property.split("_");
      }
      return [model, properties];
    },

    /**
     * Method for apply validators in set value
     */
    applyValidatorsSetValue: function(value) {
      if (CommonsUtils.arrayNotNull(this.validatorsInput)) {
        for (let i = 0; i < this.validatorsInput.length; i++) {
          value = this.validatorsInput[i].validate(value, this);
        }
      }
      return value;
    },

    /**
     * Method for apply converters in set value
     */
    applyConvertersSetValue: function(value) {
      if (CommonsUtils.arrayNotNull(this.convertersInput)) {
        for (let i = 0; i < this.validatorsInput.length; i++) {
          value = this.convertersInput[i].convertAsObject(value, this);
        }
      }
      return value;
    },

    /**
     * Method for apply converters in get value
     */
    applyConvertersGetValue: function(value) {
      if (CommonsUtils.arrayNotNull(this.convertersInput)) {
        for (let i = 0; i < this.validatorsInput.length; i++) {
          value = this.convertersInput[i].convertAsString(value, this);
        }
      }
      return value;
    },

    /**
     * Method to resolve value for especific type input
     * @param value
     */
    resolveDataValue: function(value) {
      if (CommonsUtils.isNull(value)) {
        if (
          this.getType() === "text" ||
          this.getType() === "password" ||
          this.getType() === "textarea" ||
          this.getType() === "hashtag"
        ) {
          value = null;
        } else if (this.getType() === "number") {
          value = null;
        } else if (this.getType() === "checkbox") {
          value = false;
        } else if (this.getType() === "color") {
          value = "#ffffff";
        }
      }
      this.setValue(value);
    },

    /**
     * Method for set value in element
     * @param value to set
     */
    setValue: function(value) {
      const modelProperties = this.takeModelProperties();
      const model = modelProperties[0];
      const properties = modelProperties[1];

      if (properties.length <= 1) {
        model[properties[0]] = value;
      } else if (properties.length === 2) {
        model[properties[0]][properties[1]] = value;
      }
    },

    /**
     * Method for get value for input
     * @returns value for input
     */
    getValue: function() {
      const modelProperties = this.takeModelProperties();
      const model = modelProperties[0];
      const properties = modelProperties[1];
      let value = null;

      if (properties.length <= 1) {
        value = model[properties[0]];
      } else if (properties.length === 2) {
        value = model[properties[0]][properties[1]];
      }
      // Apply converters in get value
      return this.applyConvertersGetValue(value, this);
    },

    /**
     * Method for fix value resolving find, converters and validators
     */
    fixValue: async function(value) {
      const modelProperties = this.takeModelProperties();

      let model = modelProperties[0];
      const properties = modelProperties[1];
      let valueRender = value;
      let modelFind = null;

      // Apply converters set value
      valueRender = this.applyConvertersSetValue(valueRender);
      // Apply validators set value
      valueRender = this.applyValidatorsSetValue(valueRender);

      // If have function find find entity
      if (CommonsUtils.isNotNull(this.functionFind)) {
        modelFind = this.functionFind(valueRender);

        if (CommonsUtils.isNotNull(this.functionAfterFind)) {
          modelFind = this.functionAfterFind(modelFind, valueRender);
        }

        if (CommonsUtils.isNull(modelFind)) {
          this.addMessageError(this.i18n("rfvue.core.errors.errorFindEntity"));
          return;
        }

        // If have service find entity
      } else if (CommonsUtils.isNotNull(this.serviceInput)) {
        let filterFindData = this.resolveFilterFind(valueRender);

        modelFind = await this.serviceInput.findEntitySearch(
          filterFindData,
          this.mapPropertiesFind
        );

        if (CommonsUtils.isNotNull(this.functionAfterFind)) {
          modelFind = this.functionAfterFind(modelFind, valueRender);
        }

        if (CommonsUtils.isNull(modelFind)) {
          this.addMessageError(this.i18n("rfvue.core.errors.errorFindEntity"));
          return;
        }
      } else {
        if (CommonsUtils.isNotNull(this.functionAfterFind)) {
          modelFind = this.functionAfterFind(model, valueRender);

          if (CommonsUtils.isNull(modelFind)) {
            this.addMessageError(
              this.i18n("rfvue.core.errors.errorFindEntity")
            );
            return;
          }
        }
      }

      if (CommonsUtils.isNotNull(modelFind)) {
        if (properties.length <= 1) {
          valueRender = modelFind[properties[0]];
        } else if (properties.length === 2) {
          valueRender = modelFind[properties[1]];
        }
        if (properties.length <= 1) {
          model = modelFind;
        } else if (properties.length === 2) {
          model[properties[0]] = modelFind;
        }
      }

      this.resolveDataValue(valueRender);
    },

    /**
     * Method for resolve filter find
     */
    resolveFilterFind: function(value) {
      let filter = this.filterFind;
      if (CommonsUtils.isNull(filter)) {
        filter = {
          property: this.propertyFind,
          type: "=",
          value: null,
          andOr: "and",
          alias: null,
          propertyData: null
        };
      }
      filter.value = value;
      return filter;
    },
    /**
     * Method execute when change data input with handler view error
     * @param event is produced when change input
     */
    onChangeInputWithHandlerViewError: async function(event) {
      const self = this;
      self.getVueContext().applyAsyncFunctionWithHandlerViewError(() => {
        return self.onChangeInput(event);
      });
    },
    /**
     * Method execute before chage input
     */
    beforeChangeInput: function() {},
    /**
     * Method execute when change data input
     * @param event is produced when change input
     */
    onChangeInput: async function(event) {
      if (this.executeLostFocus && !this.disabled) {
        let value = event.target.value;

        // Invert value if checkbox
        if (this.getType() === "checkbox") {
          value = !this.value;
          event.target.checked = CommonsUtils.isNotNull(value)
            ? this.value
            : false;
        } else if (
          (this.getType() === "text" ||
            this.getType() === "password" ||
            this.getType() === "textarea") &&
          StringUtils.isBlank(value)
        ) {
          value = null;
        }

        this.fixValue(value);
      }

      this.beforeChangeInput();

      // Set execute to true
      // This propery is use for keydown quick access or suggestion box
      this.executeLostFocus = true;
    },

    /**
     * Method for resolve default converters from input
     */
    resolveDefaultConverters: async function() {},

    /**
     * Method for resolve default validators from input
     */
    resolveDefaultValidators: async function() {},

    /**
     * Method for resolve validators
     */
    resolveValidators: async function() {
      await this.resolveDefaultValidators();
      if (CommonsUtils.arrayNotNull(this.validators)) {
        for (let i = 0; i < this.validators.length; i++) {
          this.validatorsInput.push(
            await this.getVueContext().getValidator(this.validators[i])
          );
        }
      }
    },

    /**
     * Method for resolve converters
     */
    resolveConverters: async function() {
      await this.resolveDefaultConverters();
      if (CommonsUtils.arrayNotNull(this.converters)) {
        for (let i = 0; i < this.converters.length; i++) {
          this.convertersInput.push(
            await this.getVueContext().getConverter(this.converters[i])
          );
        }
      }
    },

    /**
     * Method for resolve service
     */
    resolveService: async function() {
      if (StringUtils.isNotBlank(this.service)) {
        this.serviceInput = await this.getVueContext().getService(this.service);
      }
    },
    /**
     * Method to execute on key down event with erro view handler
     * @param event
     */
    onKeyDownWithHandlerViewError: async function(event) {
      const self = this;
      self.getVueContext().applyAsyncFunctionWithHandlerViewError(() => {
        return self.onKeyDown(event);
      });
    },
    /**
     * Method to execute on key down event
     * @param event
     */
    onKeyDown: async function(event) {
      // F4
      if (
        event.keyCode === 115 &&
        !this.disabled &&
        CommonsUtils.isNotNull(this.inputButton)
      ) {
        // no execute lost focus on execute action button
        this.executeLostFocus = false;
        this.executeButton();
      } else {
        // Call default event input
        this.onKeyDownInput(event);
      }
    },

    /**
     * Method execute on key down input. Override for diferents types of inputs
     * @param event is the javascript event produce on key down
     */
    onKeyDownInput: async function(event) {
      event.target;
    },

    /**
     * Mhetod for execute button from input
     */
    executeButton: function() {
      if (CommonsUtils.isNotNull(this.inputButton)) {
        const button = document.getElementById(this.inputButton);
        if (CommonsUtils.isNotNull(button)) {
          button.click();
        }
      }
    },

    /**
     * Method for validate min max
     * @param isValid is valid for others validations
     * @returns true if valid, false if not
     */
    validateMinMax: function(isValid) {
      if (
        isValid &&
        CommonsUtils.isNotNull(this.min) &&
        CommonsUtils.isNotNull(this.max) &&
        CommonsUtils.isNotNull(this.rendered) &&
        CommonsUtils.isNotNull(this.disabled) &&
        this.max > 0 &&
        this.min >= 0 &&
        this.rendered &&
        !this.disabled
      ) {
        let value = this.getValue();
        isValid = CommonsUtils.isNotNull(this.getValue());
        if (isValid) {
          // If instance of String
          if (value instanceof String) {
            isValid = value.length < this.min || value.length > this.max;
            // Other, isntace is number
          } else {
            isValid = value < this.min || value > this.max;
          }
        }
        if (!isValid) {
          this.addMessageError(
            this.i18n("rfvue.data.minMax", [this.label, this.min, this.max])
          );
        }
      }
      return isValid;
    },
    /**
     * Method for validate required
     * @param isValid is valid for others validations
     * @returns true if valid, false if not
     */
    valideRequired: function(isValid) {
      if (
        isValid &&
        CommonsUtils.isNotNull(this.required) &&
        CommonsUtils.isNotNull(this.rendered) &&
        CommonsUtils.isNotNull(this.disabled) &&
        this.required &&
        this.rendered &&
        !this.disabled
      ) {
        isValid = CommonsUtils.isNotNull(this.getValue());
        if (!isValid) {
          this.addMessageError(this.i18n("rfvue.data.required", [this.label]));
        }
      }
      return isValid;
    },

    validate: async function() {
      let valid = true;
      // Validate required
      valid = this.valideRequired(valid);
      // Validate min/max
      valid = this.validateMinMax(valid);
      return valid;
    },
    /**
     * Method for set initial value
     */
    setInitialValue: function() {
      if (CommonsUtils.isNotNull(this.getValue())) {
        // Set initial value on create
        this.fixValue(this.getValue());
      }
    }
  },

  created: function() {
    (async () => {
      this.realSize = this.size;

      if (
        CommonsUtils.isNotNull(this.max) &&
        this.max > 0 &&
        this.realSize == 0
      ) {
        this.realSize = this.max;
      }
      // Resolve service
      await this.resolveService();
      // Resolve validators
      await this.resolveValidators();
      // Resolve converters
      await this.resolveConverters();
      // set inital value
      this.setInitialValue();
    })();
  },

  mounted: function() {
    let inputContainer = document.getElementById(this.getId());
    if (
      CommonsUtils.isNull(inputContainer) &&
      CommonsUtils.isNotNull(this.$el)
    ) {
      let element = this.$el;
      let child = null;
      for (let i = 0; i < element.childNodes.length; i++) {
        child = element.childNodes[i];
        if (CommonsUtils.isNotNull(child.id) && child.id == this.getId()) {
          inputContainer = child;
          break;
        }
      }
    }
    
    if (CommonsUtils.isNotNull(inputContainer)) {
      let self = this;
      //console.log(inputContainer);
      inputContainer[
        ConstantsComponents.KEY_EVENT_VALIDATE
      ] = async function() {
        return self.validate();
      };
      // inputContainer.addEventListener(
      //   ConstantsComponents.KEY_EVENT_VALIDATE,
      //   async function() {
      //     return self.validate();
      //   }
      // );
    }
  },

  data() {
    return {
      // Service for search data
      serviceInput: null,
      // Converters from input
      convertersInput: [],
      // validators form input
      validatorsInput: [],
      // Execute lost focus
      executeLostFocus: true,
      // Real size for component
      realSize: 0
    };
  }
};
</script>