import ViewContextFactory from "../view/ViewContextFactory";
import CommonsUtils from "../../../utils/CommonsUtils";
import StringUtils from "../../../utils/StringUtils";
/**
 * Class for manage security in context application
 */
export default class SecuirtyContextFactory extends ViewContextFactory {
  constructor() {
    super();
    this.registerSecurityInterceptor();
  }

  /**
   * Method for add token and refresh if necesary for request
   * @param {*} request is request for add refresh token
   */
  refreshAddTokenRequest(request) {
    const self = this;
    let checkToken = true;

    let isLoginRefresh = false;

    // check not contains url login
    if (StringUtils.isNotBlank(self.loginUrl)) {
      checkToken = !request.url.includes(self.loginUrl);
      isLoginRefresh = request.url.includes(self.loginUrl);
    }

    // if success check no contains refresh token url
    if (checkToken && StringUtils.isNotBlank(self.refreshTokenUrl)) {
      checkToken = !request.url.includes(self.refreshTokenUrl);
      if (!isLoginRefresh) {
        isLoginRefresh = request.url.includes(self.refreshTokenUrl);
      }
    }

    // If succes check no contains public access url
    if (checkToken && CommonsUtils.isNotNull(self.mapUrlsPublic)) {
      for (let key in self.mapUrlsPublic) {
        if (StringUtils.isNotBlank(key)) {
          checkToken = !request.url.includes(key);
          if (!checkToken) {
            break;
          }
        }
      }
    }
    // console.log(checkToken);
    // console.log(self.haveSecurity);
    if (checkToken && self.haveSecurity) {
      const token = self.getTokenInSession();
      // Send jwt token
      if (StringUtils.isNotBlank(token)) {
        // request = request.clone({
        //   setHeaders: {
        //     authorization: `Bearer ${token}`
        //   }
        // });
        if (self.isElectronApp()) {
          request.headers["authorization"] = `Bearer ${token}`;
        } else {
          request.headers.append("authorization", `Bearer ${token}`);
        }
      }

      // Refresh token if necesary
      if (
        !isLoginRefresh &&
        StringUtils.isNotBlank(self.refreshTokenUrl) &&
        self.isCallRefreshToken()
      ) {
        self.getSecurityService().refreshToken();
      }
    }
  }

  /**
   * Method for register security interceptor
   */
  registerSecurityInterceptor() {
    if (CommonsUtils.isNull(this.secuirtyInterceptor)) {
      let self = this;

      this.secuirtyInterceptor = window.fetch;
      window.fetch = function() {
        let request = arguments[0];

        // Add/refrehs token if necesary
        self.refreshAddTokenRequest(request);

        arguments[0] = request;
        // console.log(arguments);
        // console.log(self.getDecodedTokenInSession());

        // Get the parameter in arguments
        // Intercept the parameter here
        return self.secuirtyInterceptor
          .apply(this, arguments)
          .then(function(response) {
            if (CommonsUtils.isNotNull(response) && response.status == 401) {
              self.getSecurityService().logout();
            }
            return response;
          })
          .catch(function(error) {
            console.log(
              "Hubo un problema con la petición Fetch:" + error.message
            );
          });
      };
    }
  }

  /**
   * Method for apply funcion for handler in view error
   * @param {*} fn for apply
   * @param {*} tab for handler view error
   */
  applyAsyncFunctionWithHandlerViewError(fn, tab) {
    let self = this;
    (() => {
      let result = fn.apply(this, arguments);
      if (
        CommonsUtils.isNotNull(result) &&
        typeof result.then === "function" &&
        typeof result.catch === "function"
      ) {
        result = result.catch(function(error) {
          self.hideWaitStatus(tab);
          self.handleGlobalViewError(error, tab);
        });
      }
    })();
  }

  /**
   * Handle golbal view error
   * @param {*} error to handle
   * @param {*} tab
   */
  handleGlobalViewError(error, tab) {
    console.log(error);
    if (CommonsUtils.isNotNull(error)) {
      let containerSetError = CommonsUtils.isNotNull(tab)
        ? tab
        : this.appComponent;
      containerSetError.setGlobalError(error);
    }
  }

  /**
   * Method to knwo call refresh token
   * @returns true if call false if not
   */
  isCallRefreshToken() {
    let call = false;

    // For the moment dont call refesh token in electron app
    if (!this.isElectronApp()) {
      call = CommonsUtils.isNull(this.dateLastRequest);

      if (CommonsUtils.isNotNull(this.dateLastRequest)) {
        let now = new Date();
        let diff = now.getTime() - this.dateLastRequest.getTime();
        let diffExpiredToken = this.timeExpireToken - diff;
        // Call when less middle of time expire token
        if (diffExpiredToken < this.timeExpireToken / 2) {
          call = true;
        }
      }
    }

    return call;
  }

  /**
   * Method to decode jwt
   * @param token
   */
  decodeJwt(token) {
    return this.jwtHelperService.decode(token);
  }

  /**
   * Method to get token decoded in session
   */
  getDecodedTokenInSession() {
    let token = sessionStorage.getItem("token");
    if (StringUtils.isNotBlank(token)) {
      token = this.decodeJwt(token);
    }
    return token;
  }

  /**
   * Method to get expiration date for jwt
   */
  getExpirationDateJwt() {
    let decodeToken = this.getDecodedTokenInSession();
    let date = null;
    if (CommonsUtils.isNotNull(decodeToken)) {
      let tmpDate = decodeToken["exp"];
      if (CommonsUtils.isNotNull(tmpDate)) {
        date = new Date(tmpDate);
      }
    }
    return date;
  }
  /**
   * Method for get user jwt
   */
  getUserJwt() {
    let decodeToken = this.getDecodedTokenInSession();
    let user = "";
    if (CommonsUtils.isNotNull(decodeToken)) {
      let tmpUser = decodeToken["user"];
      if (CommonsUtils.isNotNull(tmpUser)) {
        user = tmpUser;
      }
    }
    return user;
  }
  /**
   * Method to set token in session
   * @param token
   */
  setTokenInSession(token) {
    sessionStorage.setItem("token", token);
  }

  /**
   * Method to remove token in session
   */
  removeTokenInSession() {
    sessionStorage.removeItem("token");
  }

  /**
   * Method to get token in session
   */
  getTokenInSession() {
    return sessionStorage.getItem("token");
  }

  /**
   * Method to get data in session
   */
  getDataInSession() {
    let dataSession = sessionStorage.getItem("dataSession");
    if (CommonsUtils.isNull(dataSession)) {
      dataSession = "{}";
    }
    return JSON.parse(dataSession);
  }

  /**
   * Method for remove data in session
   */
  removeDataInSession() {
    sessionStorage.removeItem("dataSession");
    this.initDataInSession();
  }

  /**
   * Method to init map data in session
   */
  initDataInSession() {
    sessionStorage.setItem("dataSession", JSON.stringify({}));
  }

  /**
   * Method to add data in session
   * @param mapProperties is a key value to add in data session
   */
  addDataInSession(mapProperties) {
    if (CommonsUtils.isNotNull(mapProperties)) {
      let jsonSession = this.getDataInSession();
      if (CommonsUtils.isNull(jsonSession)) {
        jsonSession = {};
      }
      for (let key in mapProperties) {
        jsonSession[key] = mapProperties[key];
      }
      sessionStorage.setItem("dataSession", JSON.stringify(jsonSession));
    }
  }

  /**
   * Method to know application is secure
   * @returns true if have security false if not
   */
  isSecure() {
    return this.haveSecurity;
  }

  /**
   * Method for get security service
   */
  getSecurityService() {
    return this.securityService;
  }

  /**
   * Method for set default security service for application
   * @param {*} securityService is security service to set
   */
  setSecurityService(securityService) {
    this.securityService = securityService;
  }

  /**
   * Method to know user is logged
   */
  isLogged() {
    // If not secure always is logged
    if (!this.isSecure()) {
      return true;
    }
    return this.getSecurityService().validSession();
  }

  /**
   * Method for send fetch for electron
   * @param {*} request for fetch
   */
  async fetchElectron(request) {
    const self = this;

    if (!request.url.startsWith("/")) {
      request.url = "/" + request.url;
    }
    this.refreshAddTokenRequest(request);

    if (request.body != null && request.body != undefined) {
      request.body = JSON.parse(request.body);
    }

    const response = this.getIpcRender().sendSync("fetchElectron", request);

    if (CommonsUtils.isNotNull(response) && response.statusProp == 401) {
      self.getSecurityService().logout();
    }

    return response;
  }
}
