<!--

Component base for pages

-->

<template>
  <div :class="'PageComponent' + styleClass" :id="idPage" :key="idPage">
    <slot name="pageContentTemplate"></slot>
  </div>
</template>

<script>
import BaseComponent from "../base/BaseComponent";
import StringUtils from "../../utils/StringUtils";
export default {
  extends: BaseComponent,
  name: "PageComponent",
  props: {
    styleClass: {
      type: String,
      // Default styleClass
      default: ""
    },
    idPage: {
      type: String,
      // Default id page
      default: StringUtils.uniqueId()
    }
  }
};
</script>

<style scoped>
.PageComponent {
  width: 100%;
  height: 100%;
  overflow: auto;
  margin: 0;
  padding: 0;
  float: left;
  box-sizing: border-box;
}
</style>