import SecurityContextFactory from "./security/SecurityContextFactory";
import CommonsUtils from "../../utils/CommonsUtils";
import StringUtils from "../../utils/StringUtils";
import ConstantsModals from "../../constants/ConstantsModals";
import ConstantsComponents from "../../constants/ConstantsComponents";
import LazyImport from "../../beans/LazyImport";
/**
 * Class for manage data in application
 */
export default class VueContextFactory extends SecurityContextFactory {
  constructor() {
    super();
    this.loadDefaultModals();
    this.loadDefaultComponents();
  }

  /**
   * Method for load default components
   */
  loadDefaultComponents() {
    if (CommonsUtils.isNull(this.mapLazyImport)) {
      this.mapLazyImport = {};
    }
    this.mapLazyImport[
      ConstantsComponents.KEY_TAB_VIEW_BODY_ITEM
    ] = new LazyImport(async () => {
      const module = await import(
        "../../components/tabview/TabViewBodyItemComponent.vue"
      );
      return module.default;
    });
    this.mapLazyImport[
      ConstantsComponents.KEY_TAB_VIEW_HEADER_ITEM
    ] = new LazyImport(async () => {
      const module = await import(
        "../../components/tabview/TabViewHeaderItemComponent.vue"
      );
      return module.default;
    });
  }

  /**
   * Method for load default modals
   */
  loadDefaultModals() {
    if (CommonsUtils.isNull(this.mapModalsLazyImport)) {
      this.mapModalsLazyImport = {};
    }
    // Modal
    this.mapModalsLazyImport[
      ConstantsModals.KEY_MODAL_COMPONENT
    ] = new LazyImport(async () => {
      const module = await import("../../components/modal/ModalComponent");
      return module.default;
    });
    // Modal confirm
    this.mapModalsLazyImport[
      ConstantsModals.KEY_MODAL_CONFIRM
    ] = new LazyImport(async () => {
      const module = await import(
        "../../components/modalconfirm/ModalConfirmComponent"
      );
      return module.default;
    });
  }

  /**
   * Mehtod for clear app
   */
  clearApp() {
    // Set logged to false
    this.appComponent.logged = false;
  }

  /**
   * Method to set map lazy imports
   * @param map is map for lazy imports
   */
  addMapLazyImports(map) {
    if (CommonsUtils.isNotNull(map)) {
      for (const key of Object.keys(map)) {
        this.mapLazyImport[key] = map[key];
      }
    }
  }

  /**
   * Method for add map modal lazy imports
   * @param map is map for lazy imports
   */
  addMapModalsLazyImport(map) {
    if (CommonsUtils.isNotNull(map)) {
      for (const key of Object.keys(map)) {
        this.mapModalsLazyImport[key] = map[key];
      }
    }
  }

  /**
   * Method for add map lazy imports services
   * @param {*} map is map for lazy imports
   */
  addMapServicesLazyImports(map) {
    if (CommonsUtils.isNotNull(map)) {
      for (const key of Object.keys(map)) {
        this.mapLazyImportServices[key] = map[key];
      }
    }
  }

  /**
   * Method for add map lazy imports validators
   * @param {*} map is map for lazy imports
   */
  addMapValidatorsLazyImports(map) {
    if (CommonsUtils.isNotNull(map)) {
      for (const key of Object.keys(map)) {
        this.mapLazyImportValidators[key] = map[key];
      }
    }
  }

  /**
   * Method for add map lazy imports converters
   * @param {*} map is map for lazy imports
   */
  addMapConvertersLazyImports(map) {
    if (CommonsUtils.isNotNull(map)) {
      for (const key of Object.keys(map)) {
        this.mapLazyImportConverters[key] = map[key];
      }
    }
  }

  /**
   * Method for get service map lazy imports
   * @param {*} key for service
   */
  async getService(key) {
    let service = null;
    if (StringUtils.isNotBlank(key) && key in this.mapLazyImportServices) {
      service = await this.mapLazyImportServices[key].fnLazyImport();
    }
    return service;
  }

  /**
   * Method for get component map lazy imports
   * @param {*} key for component
   */
  async getComponent(key) {
    let component = null;
    if (StringUtils.isNotBlank(key) && key in this.mapLazyImport) {
      component = await this.mapLazyImport[key].fnLazyImport();
    }
    return component;
  }

  /**
   * Method for get modal map lazy imports
   * @param {*} key for modal
   */
  async getMoldaComponent(key) {
    let modal = null;
    if (StringUtils.isNotBlank(key) && key in this.mapModalsLazyImport) {
      modal = await this.mapModalsLazyImport[key].fnLazyImport();
    }
    return modal;
  }

  /**
   * Method for get converter map lazy imports
   * @param {*} key for converter
   */
  async getConverter(key) {
    let converter = null;
    if (StringUtils.isNotBlank(key) && key in this.mapLazyImportConverters) {
      converter = await this.mapLazyImportConverters[key].fnLazyImport();
    }
    return converter;
  }

  /**
   * Method for get validator map lazy imports
   * @param {*} key for validator
   */
  async getValidator(key) {
    let validator = null;
    if (StringUtils.isNotBlank(key) && key in this.mapLazyImportValidators) {
      validator = await this.mapLazyImportValidators[key].fnLazyImport();
    }
    return validator;
  }

  /**
   * Method for set menu factory
   * @param {*} menuFactory is menu factory to set
   */
  setMenuFactory(menuFactory) {
    if (CommonsUtils.isNotNull(menuFactory)) {
      this.menuFactory = menuFactory;
    }
  }
}
