<!--

Base component with extends all components

-->
<script>
import StringUtils from "../../utils/StringUtils";
import CommonsUtils from "../../utils/CommonsUtils";
import VueContext from "../../context/VueContext";
import ConstantsLevelMessages from "../../constants/ConstantsLevelMessages";
import ConstantsStatusCodesHttp from "../../constants/ConstantsStatusCodesHttp";
import ResponseService from "../../beans/ResponseService";

export default {
  name: "BaseComponent",
  props: {
    idView: {
      type: String,
      default: null
    },
    modalComponent: {
      type: Object,
      default: null
    },
    params: {
      type: Object,
      default: null
    },
    tab: {
      type: Object,
      default: null
    }
  },

  data() {
    return {
      id: StringUtils.uniqueId()
    };
  },

  // beforeCreate: function() {
  //   let self = this;
  //   let methods = this.$options.methods || {};
  //   // console.log(methods);
  //   if (CommonsUtils.isNotNull(methods)) {
  //     for (var key in methods) {
  //       let original = methods[key];
  //       methods[key] = function() {
  //         try {
  //           let result = original.apply(this, arguments);
  //           if (CommonsUtils.isNotNull(result)) {
  //             // console.log(result);
  //             if (
  //               typeof result.then === "function" &&
  //               typeof result.catch === "function"
  //             ) {
  //               result = result.catch(function(err) {
  //                 CommonsUtils.handleError(err, self, key);
  //               });
  //             }
  //           }
  //           return result;
  //         } catch (err) {
  //           CommonsUtils.handleError(err, self, key);
  //         }
  //       };
  //     }
  //   }
  // },

  methods: {
    /**
     * Method for get VueContext
     */
    getVueContext: function() {
      return VueContext;
    },
    /**
     * Method for get id
     * @returns id from view
     */
    getId: function() {
      let id = this.idView;
      if (StringUtils.isBlank(id)) {
        id = this.id;
      }
      return id;
    },
    /**
     * Method for get id list
     * @returns id for list
     */
    getIdList: function() {
      return this.getId() + "List";
    },
    /**
     * Method for get id red
     * @returns id for red
     */
    getIdRed: function() {
      return this.getId() + "Red";
    },
    /**
     * Method for get tab
     * @returns tab for component
     */
    getTab: function() {
      let tab = this.tabView;
      if (CommonsUtils.isNull(tab)) {
        tab = this.tab;
      }
      return tab;
    },
    /**
     * Method for transalte text
     * @param text to translate
     * @param params inyect in text to translate
     * @returns text transalte
     */
    i18n: function(text, params) {
      let textTranslate = text;
      if (StringUtils.isNotBlank(text) && CommonsUtils.isNotNull(VueContext)) {
        textTranslate = VueContext.i18n.translate(text, params);
      }
      return textTranslate;
    },

    /**
     * Method for set focus in firt input
     */
    setFocusFirstInput() {
      let self = this;
      setTimeout(() => {
        self
          .getVueContext()
          .setFocusFirtsInput([
            self.getIdList(),
            self.getIdRed(),
            self.getId()
          ]);
      }, 1);
    },

    /**
     * Method for set focus in firt input no wait
     */
    setFocusFirstInputNoWait() {
      let self = this;

      self
        .getVueContext()
        .setFocusFirtsInput([self.getIdList(), self.getIdRed(), self.getId()]);
    },

    /**
     * Method for know component is modal
     */
    isModal: function() {
      return CommonsUtils.isNotNull(this.modalComponent);
    },

    /**
     * Method for close modal
     */
    closeModal: function() {
      if (CommonsUtils.isNotNull(this.modalComponent)) {
        this.modalComponent.closeModalComponent(null, true);
      }
    },

    /**
     * Method for resolve data access await
     */
    resolveDataAccessAwait: async function(fnData, keyResolveData) {
      if (!CommonsUtils.isNotNull(keyResolveData)) {
        keyResolveData = "data";
      }
      const dataReturn = new ResponseService();
      try {
        let data = await fnData();

        if (this.getVueContext().isElectronApp()) {
           data = JSON.parse(data);
          // check status http is 200 or 201
          if (
            CommonsUtils.isNotNull(data) &&
            (data.statusProp === ConstantsStatusCodesHttp.OK ||
              data.statusProp === ConstantsStatusCodesHttp.CREATED)
          ) {
            const json = data.jsonProp;
            dataReturn.data = json[keyResolveData];
            dataReturn.statusCode = data.statusProp;
          } else if (CommonsUtils.isNotNull(data)) {
            dataReturn.statusCode = data.statusProp;
          }
        } else {
          // check status http is 200 or 201
          if (
            CommonsUtils.isNotNull(data) &&
            (data.status === ConstantsStatusCodesHttp.OK ||
              data.status === ConstantsStatusCodesHttp.CREATED)
          ) {
            const json = await data.json();
            dataReturn.data = json[keyResolveData];
            dataReturn.statusCode = data.status;
          } else if (CommonsUtils.isNotNull(data)) {
            dataReturn.statusCode = data.status;
          }
        }
      } catch (ex) {
        dataReturn.errorMsg = ex.message;
      }
      return dataReturn;
    },

    /**
     * Method for show wait status
     */
    showWaitStatus: function() {
      VueContext.showWaitStatus(this.tab);
    },

    /**
     * Method for hide wait status
     */
    hideWaitStatus: function() {
      VueContext.hideWaitStatus(this.tab);
    },

    /**
     * Method for hide tooltips
     */
    hideToolTips: function() {
      VueContext.hideToolTips();
    },

    /**
     * Method for add message success
     */
    addMessageSucces: function(message) {
      this.addMessage(message, ConstantsLevelMessages.SUCCES);
    },

    /**
     * Method for add message info
     */
    addMessageInfo: function(message) {
      this.addMessage(message, ConstantsLevelMessages.INFO);
    },

    /**
     * Method for add message error
     */
    addMessageError: function(message) {
      this.addMessage(message, ConstantsLevelMessages.ERROR);
    },

    /**
     * Method for add message warning
     */
    addMessageWarning: function(message) {
      this.addMessage(message, ConstantsLevelMessages.WARNING);
    },

    /**
     * Method for add message
     * @param message to add
     * @param level level message, success, info, error ...
     */
    addMessage: function(message, level) {
      // console.log(this.tab);
      VueContext.addMessage(message, level, this.tab);
    },

    /**
     * Method for get extra data from context
     * @param key from extra data from context
     * @return extra data from context
     */
    getExtraData: function(key) {
      let result = null;
      if (CommonsUtils.isNotNull(this.getVueContext())) {
        result = this.getVueContext().getExtraData(key);
      }
      return result;
    }
  }
};
</script>