/**
 * Class for commons utilities
 */
export default class CommonsUtils {
  /**
   * Method to check data is not null
   * @param data is value to check if not null
   */
  static isNotNull(data) {
    return data != null && data != undefined;
  }

  /**
   * Method to check data is  null
   * @param data is value to check is null
   */
  static isNull(data) {
    return data == null || data == undefined;
  }

  /**
   * Method to check array is not null and length > 0
   * @param data is value to check if not null
   */
  static arrayNotNull(data) {
    return CommonsUtils.isNotNull(data) && data.length > 0;
  }

  /**
   * Method for handle error
   * @param {*} error
   * @param {*} vm
   * @param {*} info
   */
  static handleError(error, vm, info) {
    let cur = vm;
    let maxNumberErrorHandler = 10;
    let counterError = 0;
    while ((cur = cur.$parent) && counterError < maxNumberErrorHandler) {
      let hooks = cur.$options.errorCaptured || [];
      for (let hook of hooks)
        if (hook.call(cur, error, vm, info) === false) break;
      counterError = counterError + 1;
    }
  }
}
