import CommonsUtils from "./CommonsUtils";
/**
 * Class for utilities string
 */
export default class StringUtils {
  constructor() {}

  /**
   * Method to check string is not null and not empty
   * @param data is value to check if not null
   */
  static isNotBlank(data) {
    return CommonsUtils.isNotNull(data) && data.trim() !== "";
  }

  /**
   * Method to check string is  null or  empty
   * @param data is value to check if  null or empty
   */
  static isBlank(data) {
    return CommonsUtils.isNull(data) || data.trim() == "";
  }

  /**
   * Method to generate a unique id concatenate chr 4 8 times separated by '-'
   * @returns A unique id in almost all probability
   */
  static uniqueId() {
    return (
      this.chr4() +
      this.chr4() +
      "-" +
      this.chr4() +
      "-" +
      this.chr4() +
      "-" +
      this.chr4() +
      "-" +
      this.chr4() +
      this.chr4() +
      this.chr4()
    );
  }

  /**
   * Method to generate random chr4 text strings
   *  @returns a random chr4 string
   */
  static chr4() {
    return Math.random()
      .toString(16)
      .slice(-4);
  }

  /**
   * Method to generate random unique id without separators
   * @returns A unique id in almost all probability
   */
  static randomId() {
    return Math.random()
      .toString(36)
      .slice(2);
  }
}
