<template>
  <div class="GlobalErrorComponent">
    <slot
      v-if="err || externalError"
      name="GlobalErrorComponent"
      v-bind:err="err"
      v-bind:vm="vm"
      v-bind:info="info"
    >{{i18n('rfvue.view.error')}}</slot>
    <slot v-else></slot>
  </div>
</template>

<script>
import BaseComponent from "../base/BaseComponent";

export default {
  extends: BaseComponent,
  name: "GlobalErrorComponent",

  props: {
    continuePropagation: {
      type: Boolean,
      // Default false continue propagation error
      default: false
    },
    externalError: {
      type: Error,
      default: null
    },
    showLogout: {
      type: Boolean,
      default: true
    }
  },

  data() {
    return {
      err: false,
      vm: null,
      info: null
    };
  },

  /**
   * Method cath erros produces in application
   * @param err message error
   * @param vm component with produces error
   * @param info specific information about error
   */
  errorCaptured(err, vm, info) {
    this.err = err;
    this.vm = vm;
    this.info = info;
    console.log(err);
    return this.continuePropagation;
  }
};
</script>

<style scoped>
.GlobalErrorComponent {
  float: left;
  width: 100%;
  height: 100%;
  background: transparent;
  border: none;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
</style>