<!--

Component base application

-->
<template>
  <GlobalErrorComponent :externalError="error">
    <!-- Style component for application-->
    <StyleComponent />
    <div class="BaseAppComponent">
      <!-- If not loggied and has security -->
      <div class="BaseAppComponentFullContainer" v-if="haveSecurity() && !logged">
        <div class="BaseAppComponentFullContainer" v-if="viewPublicName == 'logging'">
          <div class="BaseAppComponentFullContainer" v-if="defaultRenderLogging">
            <LoginComponent v-bind:appComponent="this" />
          </div>
          <slot name="baseapploggingTemplate" v-else></slot>
        </div>
        <!-- Public view distinct logging view -->
        <div class="BaseAppComponentFullContainer" v-else>
          <slot name="baseapppublicTemplate"></slot>
        </div>
      </div>
      <!-- If loggged or has not security -->
      <div class="BaseAppComponentFullContainer" v-else>
        <div class="BaseAppComponentFullContainer" v-if="defaultRenderApp">
          <!-- Header component -->
          <HeaderComponent>
            <template slot="headerLeftTemplate">
              <slot name="headerLeftTemplate" v-bind:app="this"></slot>
            </template>
            <template slot="headerBodyTemplate">
              <slot name="headerBodyTemplate" v-bind:app="this"></slot>
            </template>
          </HeaderComponent>
          <!-- Menu component -->
          <MenuComponent>
            <template slot="menuContentTemplate">
              <slot name="menuContentTemplate" v-bind:app="this"></slot>
            </template>
          </MenuComponent>
          <!-- Body component -->
          <BodyComponent>
            <template slot="bodyContentTemplate">
              <TabViewComponent />
            </template>
          </BodyComponent>
        </div>
        <slot name="baseappTemplate" v-else></slot>
      </div>
      <!--Modals for application-->
      <div class="TabViewBodyItemComponentModals" id="modals"></div>
    </div>
  </GlobalErrorComponent>
</template>

<script>
import GlobalErrorComponent from "../../error/GlobalErrorComponent";
import StyleComponent from "../../style/StyleComponent";
import HeaderComponent from "../../header/HeaderComponent";
import VueContext from "../../../context/VueContext";
import LoginComponent from "../../login/LoginComponent";
import CommonsUtils from "../../../utils/CommonsUtils";
import BaseComponent from "../BaseComponent";
import MenuComponent from "../../menu/MenuComponent";
import BodyComponent from "../../body/BodyComponent";
import TabViewComponent from "../../tabview/TabViewComponent";

export default {
  extends: BaseComponent,
  name: "BaseAppComponent",

  props: {
    defaultRenderLogging: {
      type: Boolean,
      // Default true render logging
      default: true
    },
    defaultRenderApp: {
      type: Boolean,
      // Default true render app
      default: true
    }
  },

  components: {
    GlobalErrorComponent,
    StyleComponent,
    HeaderComponent,
    LoginComponent,
    MenuComponent,
    BodyComponent,
    TabViewComponent
  },

  created: function() {
    // Set application for context
    this.getVueContext().appComponent = this;
    this.logged = this.getVueContext().isLogged();

    // load config for app
    this.loadConfigApp();
  },

  data() {
    return {
      logged: false,
      viewPublicName: "logging",
      error: null
    };
  },

  methods: {
    /**
     * Method for set global error
     */
    setGlobalError: function(error) {
      this.error = error;
    },
    /**
     * Method change public view in base app
     * @param name is the name public view render in base app
     */
    changePublicView: function(name) {
      this.viewPublicName = name;
    },

    /**
     * Method to get name for app
     * @returns name for app
     */
    appName: function() {
      return VueContext.appName;
    },

    /**
     * Method to know has security
     * @returns true if has security false if not
     */
    haveSecurity: function() {
      return VueContext.isSecure();
    },

    /**
     * Method for load configuration for app
     */
    loadConfigApp: function() {},

    /**
     * Method for load post login
     */
    loadPostLogin: function() {},

    /**
     * Method to get VueContext
     * @returns VueContext
     */
    getVueContext: function() {
      return VueContext;
    },

    /**
     * Method launch post login
     * @param data
     */
    functionPostLogin: function(data) {
      if (CommonsUtils.isNotNull(data)) {
        VueContext.addDataInSession(data.data);
      }
      this.loadPostLogin();
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.BaseAppComponent,
.BaseAppComponentFullContainer {
  float: left;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
</style>
