import Vue from "vue";
import Vuex from "vuex";

import VueContextFactory from "../factory/context/VueContextFactory";

// Load vuex config
Vue.use(Vuex);

const VueContext = new Vuex.Store({
  state: {
    vueContextFactory: new VueContextFactory()
  }
});

export default VueContext.state.vueContextFactory;
