<!--
 Component input text
-->

<template>
  <div v-if="rendered" class="InputComponent">
    <!-- Label -->
    <div class="InputLabelComponent">
      <div v-if="renderLabel">{{label}}</div>
    </div>

    <!-- Input -->
    <input
      autocomplete="off"
      :id="getId()"
      type="text"
      :required="required"
      :disabled="disabled"
      :size="realSize"
      :label="label"
      :maxLength="max"
      :minLength="min"
      :class="getDefaultStyleClass() + ' ' + styleClass"
      v-on:change="onChangeInputWithHandlerViewError"
      v-on:keydown="onKeyDownWithHandlerViewError($event)"
      :value="getValue()"
    />

    <!-- Required -->
    <div v-if="!disabled && required" class="InputComponentRequired">*</div>
  </div>
</template>

<script>
import BaseInputComponent from "../base/baseinput/BaseInputComponent";
export default {
  extends: BaseInputComponent,
  name: "InputTextComponent",
  methods: {
    /**
     * Method for get type for input
     * @returns text
     */
    getType: function() {
      return "text";
    },
    /**
     * Method for get default class
     * @returns default class for input
     */
    getDefaultStyleClass: function() {
      return "InputTextComponent";
    }
  }
};
</script>

<style scoped>
.InputTextComponent {
  float: left;
  clear: left;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  background: var(--rf-inputBackColor, white);
  color: var(--rf-inputFontColor, black);
  box-sizing: border-box;
  height: 30px;
  border-radius: 5px;
  border: 1px solid var(--rf-inputBorderColor, #466289);
  padding: 6px;
}

.InputTextComponent[disabled] {
  color: var(--rf-inputFontColorDisabled, black);
  background: var(--rf-inputBackColorDisabled, #afacac);
  opacity: 0.7;
}

.InputTextComponent:focus {
  border: var(--rf-inputBorderColorFocus, #fff) 1px solid;
  box-sizing: border-box;
  background: var(--rf-inputBackColorFocus, #466289);
  color: var(--rf-inputFontColorFocus, white);
}
</style>