<!--
    Component for show hide tooltip
-->
<template>
  <span v-bind:id="getId()" class="ToolTipComponent"></span>
</template>
<script>
import BaseComponent from "../base/BaseComponent";
import CommonsUtils from "../../utils/CommonsUtils";
export default {
  extends: BaseComponent,
  name: "ToolTipComponent",

  props: {
    for: {
      type: String,
      default: null
    },
    value: {
      type: String,
      default: null
    }
  },

  mounted() {
    const self = this;
    setTimeout(() => {
      self.hoverComponent();
    }, 1);
  },

  methods: {
    /**
     * Method for hover component
     */
    hoverComponent: async function() {
      let parent = null;

      if (CommonsUtils.isNotNull(this.for)) {
        parent = document.getElementById(this.for);
      } else {
        parent = document.getElementById(this.getId());
        if (CommonsUtils.isNotNull(parent)) {
          parent = parent.parentNode;
        }
      }

      if (CommonsUtils.isNotNull(parent)) {
        let self = this;

        parent.onmouseover = function(event) {
          if (event != null) {
            let toolTipContainer = document.getElementById("tooltipcontainer");
            if (toolTipContainer == null || toolTipContainer == undefined) {
              toolTipContainer = document.createElement("DIV");
              toolTipContainer.classList.add("ToolTipComponentContainer");
              toolTipContainer.style.display = "none";
              toolTipContainer.id = "tooltipcontainer";
              document.body.appendChild(toolTipContainer);
            }

            toolTipContainer.innerText = self.value;

            toolTipContainer.style.left =
              parent.getBoundingClientRect().left + "px";
            toolTipContainer.style.top =
              parent.getBoundingClientRect().top +
              parent.getBoundingClientRect().height +
              5 +
              "px";
            toolTipContainer.style.display = "block";

            if (
              toolTipContainer.offsetLeft +
                toolTipContainer.getBoundingClientRect().width >
              document.body.offsetWidth
            ) {
              toolTipContainer.style.left =
                document.body.offsetWidth -
                toolTipContainer.getBoundingClientRect().width +
                "px";
            }
          }
        };

        parent.onmouseout = function(event) {
          if (event != null) {
            const toolTipContainer = document.getElementById(
              "tooltipcontainer"
            );
            if (toolTipContainer != null && toolTipContainer != undefined) {
              toolTipContainer.innerText = "";
              toolTipContainer.style.display = "none";
            }
          }
        };
      }
    }
  }
};
</script>
<style scoped>
.ToolTipComponent {
  display: none;
}
</style>