<!--

Component for header

-->
<template>
  <div class="HeaderComponent">
    <!-- Left header -->
    <div class="HeaderMenu">
      <button class="btnLogout btnHamburger fas fa-bars" v-on:click="onClickShowHideMenu">
        <ToolTipComponent :value="i18n('rfvue.header.hamburger')" />
      </button>

      <slot name="headerLeftTemplate"></slot>
    </div>

    <!-- Body header -->
    <div class="HeaderBody">
      <button v-if="renderCloseTabs" class="btnLogout fas fa-trash" v-on:click="closeAllTabs">
        <ToolTipComponent :value="i18n('rfvue.header.closeAllTabs')" />
      </button>

      <slot name="headerBodyTemplate"></slot>
    </div>

    <!-- Rigth header -->
    <div class="HeaderRight">
      <button
        v-if="this.renderLogout"
        class="btnLogout fas fa-power-off"
        v-on:click="this.prepareLogoutWithHandlerViewError"
        type="button"
      >
        <ToolTipComponent :value="i18n('rfvue.header.logout')" />
      </button>
    </div>
  </div>
</template>

<script>
import BaseComponent from "../base/BaseComponent";
import ToolTipComponent from "../tooltip/ToolTipComponent";
import ConstantsModals from "../../constants/ConstantsModals";
import CommonsUtils from "../../utils/CommonsUtils";

export default {
  extends: BaseComponent,
  name: "HeaderComponent",
  props: {
    renderLogout: {
      type: Boolean,
      default: true
    },
    renderCloseTabs: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    /**
     * Function for close all tabs for application
     */
    closeAllTabs: function() {
      this.getVueContext().closeAllTabs();
    },
    /**
     * Method for prepare logout with handler view error
     */
    prepareLogoutWithHandlerViewError: async function() {
      const self = this;
      self.getVueContext().applyAsyncFunctionWithHandlerViewError(() => {
        return self.prepareLogout();
      });
    },
    /**
     * Method for prepare logout
     */
    prepareLogout: async function() {
      this.getVueContext().hideToolTips();
      const params = {};
      params[ConstantsModals.KEY_MODAL_COMPONENT_PARAM_ACTION_COMPONENT] = this;
      params[ConstantsModals.KEY_MODAL_COMPONENT_PARAM_ACTION_YES] = "logout";

      await this.getVueContext().openConfirmModal(null, null, false, params);
    },
    /**
     * Method to show hide menu from header
     */
    onClickShowHideMenu: function() {
      if (CommonsUtils.isNotNull(this.getVueContext().headerFactory)) {
        this.getVueContext().headerFactory.showHideMenu();
      }
    },
    /**
     * Method for execuente logout
     * @returns true
     */
    logout: function() {
      this.getVueContext()
        .getSecurityService()
        .logout();
      return true;
    }
  },
  components: {
    ToolTipComponent
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.HeaderComponent {
  float: left;
  width: 100%;
  height: 50px;
  background-color: var(--rf-headerBackColor, #466289);
  box-sizing: border-box;
  display: block;
}

.HeaderRight {
  float: right;
}

.btnLogout {
  background: transparent;
  border: none;
  cursor: pointer;
  color: var(--rf-headerFontColor, white);
  font-size: 24px;
  margin: 10px;
  outline: none;
}

.btnHamburger {
  float: left;
}

.HeaderMenu {
  float: left;
  width: 150px;
}

.HeaderBody {
  float: left;
  width: calc(100% - 210px);
}
</style>
