import en from "./locales/en.json";
import CommonsUtils from "../utils/CommonsUtils";

export default class I18n {
  constructor() {
    this.locales = { en: {} };
    this.locale = "en";
    this.addTranslations([{ en: en }]);
  }

  /**
   * Method to change locale for i18n
   * @param locale is locale to change
   */
  changeLocale(locale) {
    // Only set locale if not null
    if (CommonsUtils.isNotNull(locale)) {
      this.locale = locale;
    }
  }

  /**
   * Method to add suported locale
   * @param suportedLocale is the locale is supported
   */
  addSuportedLocale(suportedLocale) {
    if (
      CommonsUtils.isNotBlank(suportedLocale) &&
      !CommonsUtils.isNotNull(this.locales[suportedLocale])
    ) {
      this.locales[suportedLocale] = {};
    }
  }

  /**
   * Method to add translation to i18n
   * @param arrayTranslations is array of map translations
   */
  addTranslations(arrayTranslations) {
    // Only set data if not null
    if (CommonsUtils.isNotNull(arrayTranslations)) {
      let translation = null;
      let locale = null;
      let fynalLocales = this.locales;
      for (let i = 0; i < arrayTranslations.length; i++) {
        translation = arrayTranslations[i];
        Object.keys(translation).forEach(function(key) {
          locale = translation[key];
          Object.keys(locale).forEach(function(keyLocale) {
            fynalLocales[key][keyLocale] = locale[keyLocale];
          });
        });
      }
      this.locales = fynalLocales;
    }
  }

  /**
   * Method to translate value
   * @param value to translate
   * @param args are to set in i18n translate
   * @returns value translate
   */
  translate(value, args) {
    let dataTranslate = value;
    if (CommonsUtils.isNotNull(value)) {
      try {
        dataTranslate = eval("this.locales[this.locale]." + value);
        // If have args to set in i18n
        if (CommonsUtils.arrayNotNull(args)) {
          for (let index = 0; index < args.length; index++) {
            try {
              dataTranslate = dataTranslate.replace(
                "{" + index + "}",
                String(args[index])
              );
            } catch (ex) {
              console.log(ex);
            }
          }
        }
      } catch (ex) {
        console.log(ex);
        dataTranslate = value;
      }
    }
    return dataTranslate;
  }
}
