import PropertiesContextFactory from "../properties/PropertiesContextFactory";
import StringUtils from "../../../utils/StringUtils";
import CommonsUtils from "../../../utils/CommonsUtils";
import ConstantsLevelMessages from "../../../constants/ConstantsLevelMessages";
import Tab from "../../../beans/Tab";
import ConstantsModals from "../../../constants/ConstantsModals";
import ConstantsComponents from "../../../constants/ConstantsComponents";
import Vue from "vue";
/**
 * Class for manage data view in application
 */
export default class ViewContextFactory extends PropertiesContextFactory {
  constructor() {
    super();
  }

  /**
   * Method to chage tab
   * @param tab is tab to change
   */
  changeTab(tab) {
    this.activeTab = tab.index;
  }

  /**
   * Method to close tab
   * @param tab is tab to close
   */
  closeTab(tab) {
    const index = tab.index;

    // Remote tabview body and header
    const tabBody = document.getElementById("tabBody" + tab.index);
    const tabHeader = document.getElementById("tabHeader" + tab.index);

    if (CommonsUtils.isNotNull(tabBody)) {
      tabBody.remove();
    }

    if (CommonsUtils.isNotNull(tabHeader)) {
      tabHeader.remove();
    }

    this.tabs.splice(index, 1);
    const numTabs = this.tabs.length - 1;
    if (numTabs < 0) {
      this.activeTab = -1;
    } else {
      if (this.activeTab === index) {
        if (index > numTabs) {
          this.activeTab = index - 1;
        } else if (index < numTabs) {
          this.activeTab = index;
        } else {
          this.activeTab = 0;
        }
      } else {
        if (this.activeTab > index) {
          this.activeTab = this.activeTab - 1;
        }
      }
    }
    if (this.activeTab > numTabs) {
      this.activeTab = 0;
    }
    this.renumerateTabs();
  }

  /**
   * Method to renumerate tabs
   */
  renumerateTabs() {
    for (let i = 0; i < this.tabs.length; i++) {
      this.tabs[i].index = i;
    }
  }

  /**
   * Method to get z-index updated
   * @returns updated z-idex
   */
  getUpdatedZIndex() {
    return this.zIndex++;
  }

  /**
   * Method to get z-index updated casted string
   * @returns updated z-idex casted string
   */
  getUpdatedZIndexStr() {
    return "" + this.getUpdatedZIndex();
  }

  /**
   * Method to know have tabs
   * @returns true if have tabs
   */
  haveTabs() {
    return CommonsUtils.isNotNull(this.tabs) && this.tabs.length > 0;
  }

  /**
   * Method to add message in view
   * @param message
   * @param level
   * @param tab
   */
  addMessage(message, level, tab) {
    let messages = null;
    if (CommonsUtils.isNotNull(tab)) {
      messages = document.getElementById("messages" + tab.index);
      if (!CommonsUtils.isNotNull(messages)) {
        messages = document.createElement("messages");
        messages.id = "messages" + tab.index;
        messages.classList.add("MessagesComponent");
        document.getElementById("tabBody" + tab.index).appendChild(messages);
      }
    } else {
      messages = document.getElementById("messages");
      if (CommonsUtils.isNull(messages)) {
        messages = document.createElement("messages");
        messages.id = "messages";
        messages.classList.add("MessagesComponent");
        document.body.appendChild(messages);
      }
    }

    let className = "";
    if (level === ConstantsLevelMessages.SUCCES) {
      className = "MessagesSucces";
    } else if (level === ConstantsLevelMessages.INFO) {
      className = "MessagesInfo";
    } else if (level === ConstantsLevelMessages.WARNING) {
      className = "MessagesWarning";
    } else if (level === ConstantsLevelMessages.ERROR) {
      className = "MessagesError";
    }

    const id = StringUtils.uniqueId();

    const messagesToast = document.createElement("DIV");
    messagesToast.classList.add("Messages");
    messagesToast.classList.add(className);
    messagesToast.style.zIndex = this.getUpdatedZIndexStr();
    messagesToast.id = id;

    const messagesToastIcon = document.createElement("DIV");
    messagesToastIcon.classList.add("MessagesIcon");

    const messagesToastIconClose = document.createElement("DIV");
    messagesToastIconClose.classList.add("MessagesIconClose");
    messagesToastIconClose.innerHTML =
      '<button class="MessagesIconClose" onClick="' +
      this.closeMessageToast(id) +
      '" ><i class="fas fa-times"></i></button>';
    messagesToastIcon.appendChild(messagesToastIconClose);

    messagesToast.appendChild(messagesToastIcon);

    const messagesToastTitle = document.createElement("DIV");
    messagesToastTitle.classList.add("MessagesTitle");

    const messagesToastTitleContent = document.createElement("DIV");
    messagesToastTitleContent.classList.add("MessagesTitleContent");
    messagesToastTitleContent.innerText = message;
    messagesToastTitle.appendChild(messagesToastTitleContent);

    messagesToast.appendChild(messagesToastTitle);

    messages.appendChild(messagesToast);

    const closeFunction = this.closeMessageToast(id);

    // Remove div with timeout funcion
    setTimeout(function() {
      eval(closeFunction);
    }, 15000);
  }

  /**
   * Method to close messages in view
   * @param id from div close toast
   */
  closeMessageToast(id) {
    return (
      "let messagesToast = document.getElementById('" +
      id +
      "');" +
      "if (messagesToast != null && messagesToast != undefined) {" +
      "messagesToast.parentElement.removeChild(messagesToast);" +
      "}"
    );
  }

  /**
   * Method to hide tooltips
   */
  hideToolTips() {
    const toolTipContainer = document.getElementById("tooltipcontainer");
    if (toolTipContainer != null && toolTipContainer != undefined) {
      toolTipContainer.innerText = "";
      toolTipContainer.style.display = "none";
    }
  }

  /**
   * Method to show wait status
   */
  async showWaitStatus(tab) {
    const tabStatus = CommonsUtils.isNotNull(tab) ? tab : this.getActiveTab();
    if (CommonsUtils.isNotNull(tabStatus)) {
      const spinnerDiv = document.getElementById(
        "spinnerDiv" + tabStatus.index
      );
      if (!CommonsUtils.isNotNull(spinnerDiv)) {
        const span = document.createElement("span");
        span.id = "spinnerDiv" + tabStatus.index;
        span.innerHTML =
          '<div class="fullDivSpinnerhourglass"> ' +
          '<div class="fullDivSpinnerhourglassContainer">' +
          '<div class="lds-hourglass"></div>' +
          "</div>" +
          "</div>";
        const tabBodyComponent = document.getElementById(
          "tabBody" + tabStatus.index
        );
        if (CommonsUtils.isNotNull(tabBodyComponent)) {
          tabBodyComponent.appendChild(span);
        }
      }
    } else {
      const spinnerDiv = document.getElementById("spinneDiv");
      if (!CommonsUtils.isNotNull(spinnerDiv)) {
        const span = document.createElement("span");
        span.id = "spinnerDiv";
        span.innerHTML =
          '<div class="fullDivSpinnerhourglass"> ' +
          '<div class="fullDivSpinnerhourglassContainer">' +
          '<div class="lds-hourglass"></div>' +
          "</div>" +
          "</div>";
        document.body.appendChild(span);
      }
    }
  }

  /**
   * Method to hide wait status
   * @param tab
   */
  async hideWaitStatus(tab) {
    const tabStatus = CommonsUtils.isNotNull(tab) ? tab : this.getActiveTab();
    let spinner = null;

    if (CommonsUtils.isNotNull(tabStatus)) {
      spinner = document.getElementById("spinnerDiv" + tabStatus.index);
    } else {
      spinner = document.getElementById("spinnerDiv");
    }

    if (CommonsUtils.isNotNull(spinner)) {
      spinner.parentElement.removeChild(spinner);
    }
  }

  /**
   * Method for valida container
   * @param {*} idContainer  for validate
   * @returns true if valid false if no
   */
  async validateContainer(idContainer) {
    let valid = true;
    let container = document.getElementById(idContainer);
    if (CommonsUtils.isNotNull(container)) {
      let elements = [];
      // Find input elelemtns
      let validateElements = container.querySelectorAll(
        'input:not([type="button"]):not([disabled])'
      );
      if (CommonsUtils.arrayNotNull(validateElements)) {
        validateElements.forEach(element => {
          elements.push(element);
        });
      }
      // Find text area elements
      validateElements = container.querySelectorAll("textarea:not([disabled])");
      if (CommonsUtils.arrayNotNull(validateElements)) {
        if (!CommonsUtils.arrayNotNull(elements)) {
          elements = validateElements;
        } else {
          validateElements.forEach(element => {
            elements.push(element);
          });
        }
      }
      if (CommonsUtils.arrayNotNull(elements)) {
        // console.log(elements);
        // const event = new Event(ConstantsComponents.KEY_EVENT_VALIDATE);
        let idComponentValid = null;
        let validElement = true;
        for (let i = 0; i < elements.length; i++) {
          validElement = await elements[i][
            ConstantsComponents.KEY_EVENT_VALIDATE
          ]();
          if (!validElement && CommonsUtils.isNull(idComponentValid)) {
            idComponentValid = elements[i].id;
          }
        }
        if (CommonsUtils.isNotNull(idComponentValid)) {
          try {
            valid = false;
            let element = document.getElementById(idComponentValid);
            element.focus();
            element.select();
          } catch (ex) {
            console.log(ex);
          }
        }
      }
    }
    return valid;
  }

  /**
   * Method for set focus first input valid
   * @param {*} arrIdContainers arr id containers set focus
   */
  setFocusFirtsInput(arrIdContainers) {
    let elements = null;
    // first find for id
    let container = null;

    if (CommonsUtils.arrayNotNull(arrIdContainers)) {
      for (let i = 0; i < arrIdContainers.length; i++) {
        container = document.getElementById(arrIdContainers[i]);
        if (CommonsUtils.isNotNull(container)) {
          break;
        }
      }
    }

    if (CommonsUtils.isNotNull(container)) {
      elements = container.querySelectorAll(
        'input:not([type="button"]):not([disabled])'
      );
    }

    if (CommonsUtils.arrayNotNull(elements)) {
      try {
        elements[0].focus();
        elements[0].select();
      } catch (ex) {
        console.log(ex);
      }
    }
  }

  /**
   * Method to add tab
   * @param title is a tile for tab
   * @param componentView is a component for body tab
   * @param moduleName is a module for component view
   */
  async addTab(title, componentView, moduleName) {
    // For force show error
    // moduleName = "test";
    // if (moduleName == "test") {
    //   throw new Error("test");
    //   //return Promise.reject(new Error('fail'));
    // }
    if (
      CommonsUtils.isNotNull(title) &&
      CommonsUtils.isNotNull(componentView) &&
      CommonsUtils.isNotNull(moduleName)
    ) {
      // set index, create tab and add title
      const index = this.tabs.length;
      const tab = new Tab();
      tab.index = index;
      tab.title = title;

      const lazyImport = this.mapLazyImport[componentView];
      let tabViewBodyItem = this.mapLazyImport[
        ConstantsComponents.KEY_TAB_VIEW_BODY_ITEM
      ];
      let tabViewHeaderItem = this.mapLazyImport[
        ConstantsComponents.KEY_TAB_VIEW_HEADER_ITEM
      ];

      if (
        CommonsUtils.isNotNull(lazyImport) &&
        CommonsUtils.isNotNull(tabViewHeaderItem) &&
        CommonsUtils.isNotNull(tabViewBodyItem)
      ) {
        let component = await lazyImport.fnLazyImport();
        tabViewHeaderItem = await tabViewHeaderItem.fnLazyImport();
        tabViewBodyItem = await tabViewBodyItem.fnLazyImport();

        const tabViewHeader = document.getElementById("TabViewHeader");
        const tabViewBody = document.getElementById("TabViewBody");

        if (
          CommonsUtils.isNotNull(tabViewHeader) &&
          CommonsUtils.isNotNull(tabViewBody) &&
          CommonsUtils.isNotNull(component)
        ) {
          this.activeTab = tab.index;
          this.tabs.push(tab);

          component = Vue.extend(component);
          tabViewHeaderItem = Vue.extend(tabViewHeaderItem);
          tabViewBodyItem = Vue.extend(tabViewBodyItem);
          // Create header item
          tabViewHeaderItem = new tabViewHeaderItem({
            propsData: { first: tab.index == 0, tab: tab }
          });
          // Create body item
          tabViewBodyItem = new tabViewBodyItem({
            propsData: { first: tab.index == 0, tab: tab }
          });

          component = new component({
            propsData: { tab: tab }
          });

          // Moount header
          tabViewHeaderItem.$mount();
          // Moount body
          tabViewBodyItem.$mount();
          // Append childs
          tabViewHeader.appendChild(tabViewHeaderItem.$el);
          tabViewBody.appendChild(tabViewBodyItem.$el);
          // Mount component
          component.$mount();
          document
            .getElementById("tabBodyContainer" + tab.index)
            .appendChild(component.$el);

          component.setFocusFirstInputNoWait();
        }
      }
    }
  }

  /**
   * Method to open confim modal
   * @param title title for confirm modal
   * @param text is a text to show in cinfirm
   * @param activeTab true if show in active tab false if not
   * @param params extra params to set in component
   */
  async openConfirmModal(title, text, activeTab, params) {
    if (!CommonsUtils.isNotNull(params)) {
      params = {};
    }
    if (StringUtils.isBlank(title)) {
      title = this.i18n.translate("rfvue.modals.confirm", null);
    }
    if (StringUtils.isBlank(text)) {
      text = this.i18n.translate("rfvue.modals.confirmQuestion", null);
    }
    params[ConstantsModals.KEY_MODAL_COMPONENT_PARAM_TEXT] = text;
    params[ConstantsModals.KEY_MODAL_COMPONENT_PARAM_STYLE_CLASS_MODAL] =
      "ConfirmModal";

    await this.addModal(
      title,
      ConstantsModals.KEY_MODAL_CONFIRM,
      ConstantsModals.KEY_MODULE_RF_VUE,
      activeTab,
      params
    );
  }

  /**
   * Method to add modal
   * @param title is title for modal
   * @param componentView is the component to render inside modal
   * @param moduleName is the module name for load component view
   * @param activeTab is open in active tab
   * @param params params to set in component modal
   */
  async addModal(title, componentView, moduleName, activeTab, params) {
    if (
      CommonsUtils.isNotNull(title) &&
      CommonsUtils.isNotNull(componentView) &&
      CommonsUtils.isNotNull(moduleName)
    ) {
      if (CommonsUtils.isNull(params)) {
        params = {};
      }
      activeTab = CommonsUtils.isNotNull(activeTab) ? activeTab : true;

      let lazyImport = this.mapModalsLazyImport[componentView];
      // If lazy import is null find in componets
      if (CommonsUtils.isNull(lazyImport)) {
        lazyImport = this.mapLazyImport[componentView];
      }
      let lazyImportModal = this.mapModalsLazyImport[
        ConstantsModals.KEY_MODAL_COMPONENT
      ];

      if (
        CommonsUtils.isNotNull(lazyImport) &&
        CommonsUtils.isNotNull(lazyImportModal)
      ) {
        let moduleViewModal = await lazyImportModal.fnLazyImport();
        let component = await lazyImport.fnLazyImport();

        let styleClass = "";

        if (
          CommonsUtils.isNotNull(
            params[ConstantsModals.KEY_MODAL_COMPONENT_PARAM_STYLE_CLASS_MODAL]
          )
        ) {
          styleClass =
            params[ConstantsModals.KEY_MODAL_COMPONENT_PARAM_STYLE_CLASS_MODAL];
        }

        let idComponentFocusClose = null;

        if (
          CommonsUtils.isNotNull(params[ConstantsModals.KEY_FOCUS_COMPONENT])
        ) {
          idComponentFocusClose = params[ConstantsModals.KEY_FOCUS_COMPONENT];
        }

        moduleViewModal = Vue.extend(moduleViewModal);

        moduleViewModal = new moduleViewModal({
          propsData: {
            tab: activeTab ? this.getActiveTab() : null,
            title: title,
            styleClass: styleClass,
            idComponentFocusClose: idComponentFocusClose
          }
        });

        // Mount modal
        moduleViewModal.$mount();

        let idContainerModal = null;

        if (activeTab) {
          idContainerModal = "modals" + this.getActiveTab().index;
        } else {
          idContainerModal = "modals";
        }

        document
          .getElementById(idContainerModal)
          .appendChild(moduleViewModal.$el);

        // Set focus first input
        moduleViewModal.focusFirstInput();

        component = Vue.extend(component);

        component = new component({
          propsData: {
            tab: activeTab ? this.getActiveTab() : null,
            params: params,
            modalComponent: moduleViewModal
          }
        });

        // Mount component
        component.$mount();

        document
          .getElementById(
            moduleViewModal.getId() + "ModalComponentBodyContainer"
          )
          .appendChild(component.$el);

        component.setFocusFirstInputNoWait();

        let modalContainer = document.getElementById(
          moduleViewModal.getId() + "ModalContainer"
        );

        let modalMask = document.getElementById(
          moduleViewModal.getId() + "Mask"
        );

        let factor = 120;

        let widthMask = modalMask.getBoundingClientRect().width;
        let heigthMask = modalMask.getBoundingClientRect().height;

        let width = modalContainer.getBoundingClientRect().width;
        let heigth = modalContainer.getBoundingClientRect().height;

        if ((widthMask - (width + factor)) / 2 > width) {
          modalContainer.style.left = (widthMask - (width + factor)) / 2 + "px";
        } else {
          modalContainer.style.left = "100px";
        }

        if ((heigthMask - (heigth + factor)) / 2 > heigth) {
          modalContainer.style.top =
            (heigthMask - (heigth + factor)) / 2 + "px";
        } else {
          modalContainer.style.top = "100px";
        }
      }
    } else {
      this.addMessage(
        this.i18n.translate("rfvue.core.errors.actionNoAvailable", null),
        "ERROR",
        activeTab ? this.getActiveTab() : null
      );
    }
  }

  /**
   * Method to close all tabs
   */
  closeAllTabs() {
    let numTabs = this.tabs.length;
    while (numTabs > 0) {
      this.closeTab(this.tabs[numTabs - 1]);
      numTabs = this.tabs.length;
    }
    this.activeTab = -1;
  }
}
