<!--

Component for login

-->

<template>
  <PageComponent v-bind:idPage="getId()">
    <template slot="pageContentTemplate">
      <form class="floatLeftFullDiv" novalidate onsubmit="event.preventDefault; return false;">
        <div class="LoginContainer">
          <div class="LoginContainerTitle">
            <div class="LoginContainerTitleSpan">{{appName()}}</div>
          </div>
          <div class="LoginContainerInput">
            <InputTextComponent
              v-bind:disabled="false"
              v-bind:required="true"
              v-bind:element="containerLogin"
              v-bind:property="propertyNick"
              v-bind:size="50"
              v-bind:max="50"
              v-bind:label="i18n('rfvue.login.nick')"
            />
            <InputPasswordComponent
              v-bind:disabled="false"
              v-bind:required="true"
              v-bind:element="containerLogin"
              v-bind:property="propertyPassword"
              v-bind:size="50"
              v-bind:max="50"
              v-bind:label="i18n('rfvue.login.password')"
            />
          </div>
          <div class="LoginContainerButton">
            <button
              class="LoginContainerButtonComponent"
              v-on:click="loginWithHandlerViewError"
            >{{i18n('rfvue.login.title')}}</button>
          </div>
        </div>
      </form>
    </template>
  </PageComponent>
</template>

<script>
import BaseViewControllerComponent from "../base/baseviewcontroller/BaseViewControllerComponent";
import PageComponent from "../page/PageComponent";
import VueContext from "../../context/VueContext";
import InputTextComponent from "../inputtext/InputTextComponent";
import InputPasswordComponent from "../inputpassword/InputPasswordComponent";

export default {
  extends: BaseViewControllerComponent,
  props: {
    appComponent: {
      type: Object,
      default: null
    },
    // Propertie for set data in element
    propertyNick: {
      type: String,
      default: "nickLogin"
    },
    propertyPassword: {
      type: String,
      default: "passwordLogin"
    }
  },
  data() {
    return {
      containerLogin: {
        nickLogin: "",
        passwordLogin: ""
      }
    };
  },
  methods: {
    /**
     * Method for login with hander view error
     */
    loginWithHandlerViewError: async function() {
      const self = this;
      VueContext.applyAsyncFunctionWithHandlerViewError(() => {
        return self.login();
      });
    },
    /**
     * Method for login
     */
    login: async function() {
      this.appComponent.logged = false;
      this.showWaitStatus();
      const valid = await VueContext.getSecurityService().login(
        this.containerLogin.nickLogin,
        this.containerLogin.passwordLogin,
        this.appComponent.functionPostLogin,
        this.paramsLogin
      );
      this.hideWaitStatus();
      this.nickLogin = "";
      this.passwordLogin = "";

      if (!valid || !VueContext.isLogged()) {
        this.addMessageError(this.i18n("rfvue.login.error", null));
        this.containerLogin.nickLogin = "";
        this.containerLogin.passwordLogin = "";
      } else {
        this.appComponent.logged = true;
      }
    },
    /**
     * Method to get name for app
     * @returns name for app
     */
    appName: function() {
      return VueContext.appName;
    }
  },
  components: {
    PageComponent,
    InputTextComponent,
    InputPasswordComponent
  }
};
</script>

<style scoped>
.LoginContainer {
  width: 450px;
  height: 300px;
  background-color: white;
  margin: 0 auto;
  margin-top: 10%;
  border-radius: 4px;
  border: 1px solid var(--rf-loginTitleBackground, #466289);
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
}

.LoginContainerTitle {
  float: left;
  width: 100%;
  background-color: var(--rf-loginTitleBackground, #466289);
  color: var(--rf-loginTitleFontColor, white);
  height: 60px;
  text-align: center;
  font-family: serif;
}

.LoginContainerTitleSpan {
  font-size: 32px;
  margin-top: 10px;
}

.LoginContainerInput {
  float: left;
  width: 100%;
  padding: 20px;
}

.LoginContainerButton {
  float: left;
  width: 100%;
  padding: 21px;
}

.LoginContainerButtonComponent {
  background-color: var(--rf-loginTitleBackground, #466289);
  color: var(--rf-loginTitleFontColor, white);
  height: 40px;
  width: 400px;
  border: none;
  cursor: pointer;
  font-size: 18px;
  font-family: serif;
}
</style>